@media
body{
    background-color: darkgrey;
    display: flex;
    justify-content: center;
  }
  #display{
    background-color: white;
    border: 5px solid orange;
    width: 450px;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 20px;
  }
  
  #drum-machine{
    
    background-color: white;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 5px 5px 5px 5px;
    flex-wrap: wrap;
    width: 260px;
    height: 260px;
  }
  .drum-pad{
    color: white;
    background-color: grey;
    text-align: center;
    padding-top: 20px;
    min-width: 80px;
    min-height: 60px;
    font-size: 20px;
    box-shadow: 1px 1px 5px 2px #000000;
    border-radius: 8px;
      
  }
  .drum-pad:hover{
    background-color: lightgrey;
  }
  .drum-pad:active{
    transform: scale(0.9);
      transition: transform .2s;
  }
#innerDisplay{
  background-color: grey;
  border-radius: 5px;
  display: flex;
  align-self: center;
  padding: 10px 20px 10px 20px;
  color: white;
  font-size: 20px;
}